<template>
  <div id="Index">

  
    <!-- 轮播图 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in swiperList" :key="index">
          <img :src="item.banner_img" alt="">
        </van-swipe-item>
      </van-swipe>

      <!-- 类型 -->
      <div class="pan-kinds">
        <ul>
          <li
            v-for="(item, index) in kindList"
            :key="index"
            @click="onClcikTo(index)">
            <span>
              <img :src="item.image" />
            </span>
            <span>{{ item.title }}</span>
          </li>
        </ul>
      </div>


  <!-- 名师阵容 -->
  <div class="pan-teacher">
    <div class="pan-title">名师阵容</div>
    <div v-for="(item, index) in teacherList"   :key="index" class="pan-teacher-content" @click="getTeacher(item.teacher_id)" >
      <div><img :src="item.teacher_avatar" alt=""></div>
      <div class="pan-teacher-text">
        <div class="content-name">{{item.teacher_name}}</div>
        <div class="content-intro">{{item.introduction}}</div>
      </div>
    </div>
  </div>

  <!-- 精品课程 -->
  <div class="pan-good">
    <div class="pan-good-title">精品课程</div>
    <div v-for="(item, index) in goodList"   :key="index" class="pan-good-content" @click="jingpin(item.teachers_list[0].course_basis_id)">
      <div class="pan-good-content-wrapper">
        <div>{{item.title}}</div>
        <div>共{{item.sales_num}}课时</div>
        <div>
          <img :src="item.teachers_list[0].teacher_avatar" alt="">
          <span>{{item.teachers_list[0].teacher_name}}</span>
        </div>
        <div>
          <div class="wrapper-apply">{{item.sales_num}}人已报名</div>
          <div :class="item.price =='免费'?'wrapper-offer-red':'wrapper-offer'">{{item.price|IndexHomePrice}}</div>
        </div>
      </div>
    </div>
  </div>

    <!-- 推荐课程 -->
    <div class="pan-good">
      <div class="pan-good-title">推荐课程</div>
      <div v-for="(item, index) in recommendList" 
          :key="index"
          class="pan-good-content">
        <div class="pan-good-content-wrapper">
          <div>{{item.title}}</div>
          <div>共{{item.sales_num}}课时</div>
          <div v-for="(v,i) in item.teachers_list " :key="i">
            <img :src="v.teacher_avatar" alt="">
            <span>{{v.teacher_name}}</span>
          </div>
          <div>
            <div class="wrapper-apply">{{item.sales_num}}人已报名</div>
            <div :class="item.price == 0?'wrapper-offer-red':'wrapper-offer'">{{item.price|IndexHomePrice}}</div>
          </div>
        </div>
      </div>
    </div>

  <!-- 明星讲师 -->
  <div class="pan-teacher">
    <div class="pan-title">名师阵容</div>
    <div v-for="(item, index) in superList" 
         :key="index"
         class="pan-teacher-content">
      <div><img :src="item.teacher_avatar" alt=""></div>
      <div class="pan-teacher-text">
        <div class="content-name">{{item.teacher_name}} <span>{{item.grade}}</span></div>
        <div class="content-intro">{{item.introduction}}</div>
      </div>
    </div>
  </div>
  <div class="pan-empty"></div>

  </div>
</template>

<script>

// 引入邮箱的界面

export default {
  name:"Index",
  data() {
    return {
      swiperList:[],
      kindList:[
        {
          image:"data:image/png;base64,",
          title:"特色课"
        },
        {
          image:"data:image/png;base64,",
          title:"一对一辅导"
        },
        {
          image:"data:image/png;base64,",
          title:"学习日历"
        }
      ],
      teacherList:[],
      goodList:[],
      recommendList:[],
      superList:[],
      selectIndex:0,
    
    }
  },
  // 局部过滤器
  filters:{
    // 过滤器判断 如果价格val为0就返回免费 如果大于0 就保留两位小数点
      IndexHomePrice(val){
          // console.log(val)
          if(val==0){
            return "免费"
          }else{
            // 保留后面两位小数
            return `$:${(val/100).toFixed(2)}`;
          }
          return val;
      }
  },
    methods: {
    // 点击访问到课程部分
    onClcikTo(index){
      this.selectIndex = index
      if(index == 0){
        this.$router.push("/home/kecheng")
      }
    },
    // 点击名师阵容进入不同的讲师详情页面
    getTeacher(id){
        // alert(id)
        this.$router.push({path:"/teacher",query:{id:id}})
    },
    // 点击精品课程进入详情页面
    jingpin(e){
      // alert(e)
      this.$router.push({path:'/details',query:{id:e}})
    }
 

  },
  // 局部自定义指令
  directives:{
      // "move":{
      //     bind(el){
      //         // console.log(el)
      //         el.onmousedown=function(e){
      //           // 当前参数e表述鼠标的xy坐标
      //           // console.log(e)
      //           // 首先获取当前div的xy坐标
      //           var x=e.clientX;
      //           var y=e.clientY;
      //           // 获取当前div距离上左边界的距离
      //           var divL=el.offsetLeft;
      //           var divT=el.offsetTop;
      //           // console.log(divL,divT,x,y)
      //           // 2.请求之后进行一个dom移动
      //           document.touchmove=function(e){
      //             // console.log(el.style.left)
      //             // alert(1)
      //              var x1=e.clientX-x+divL;
      //              var y1=e.clientY-y+divT;
      //              console.log(x1,y1)

      //               el.style.left=x1+"px";
      //               el.style.top=y1+'px';
      //           }
      //         }
      //     }
      // }
  },
  //生命周期 - 创建完成（访问当前this实例）
  created() {

  },
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    // 轮播接口数据渲染
    this.http.get('/api/app/banner').then((msg)=>{
      console.log(msg)
      // window.console.log(msg)
      this.swiperList=msg.data.data;
    })
    // 首页列表
    this.http.get('/api/app/recommend/appIndex').then((msg)=>{

          // console.log(msg)
          msg.data.data.forEach((item,index)=>{
           // 名师阵容
             if(item.channel_info.type==3){
                  this.teacherList=item.list;
                  // console.log(item.list)
           // 精品课程
             }else if(item.channel_info.name=="精品课程"){
                this.goodList=item.list;
                // console.log(this.goodList)
            //  推荐课程
             }else if(item.channel_info.name=="推荐课程"){
                this.recommendList=item.list;
                // console.log(item.list)
             }
            // 明星讲师
            else if(item.channel_info.name=="明星讲师"){
                this.superList=item.list;
                // console.log(item.list)
            }
          })
          // console.log(msg.data.data[1].list)
    })

  }
}

</script>

<style lang='scss'>
  /* @import url(); 引入css类 */




#Index{
    font-size: 0.14rem;
    width: 100%;
    height: 6.67rem;
    background:rgb(240,242,245);
    overflow: scroll;
}



.pan-empty
{
  width: 100%;
  height: 0.5rem;
}
/* 轮播图 */
.my-swipe .van-swipe-item 
{
  width: 100%;
  height: 1.8rem;
  background-color: red;
}
.my-swipe .van-swipe-item img
{
  width: 100%;
  height: 1.8rem;
}
/* 类型 */
.pan-kinds
{
  position:relative;
  margin-top:-0.2rem;
  margin-bottom:0.2rem;
  width: 100%;
}
.pan-kinds ul
{
  display:flex;
  justify-content: space-around;
}
.pan-kinds>ul>li
{
  width: 28%;
  height: 1rem;
  background-color: #fff;
  border-radius: 0.1rem;
  display:flex;
  justify-content: center;
  /* 横排 */
  flex-direction: column;
  text-align: center;
}
.pan-kinds>ul>li>span
{
  margin:0.07rem 0;
  color:#A1A1A1;
  font-size:0.12rem;
}
.pan-kinds>ul>li>span>img
{
  width: 0.23rem;
}
/* 名师阵容、明星讲师*/
.pan-teacher
{
  width: 100%;
  padding:0 3%;
  box-sizing: border-box;
  margin-bottom:0.2rem;
}
.pan-title
{
  border-left:3px solid red;
  padding-left:2%;
  font-size:0.16rem;
}
.pan-teacher-content
{
  width: 100%;
  height: 1rem;
  background-color: #fff;
  margin-top:0.1rem;
  border-radius: 0.1rem;
  display:flex;
  justify-content: flex-start;
  align-items: center;
}
.pan-teacher-text{
  width: 80%;
 
}
.pan-teacher-text>div:nth-of-type(1){
  height: 0.3rem;
  line-height: 0.3rem;
  padding-left: 0.1rem;
  box-sizing: border-box;
  margin-top: 0.05rem;
}

.pan-teacher-content>div:nth-child(1)
{
  width: 20%;
  display:flex;
  justify-content: center;
  align-items: center;
}
.pan-teacher-content>div:nth-child(1)>img
{
  width: 0.45rem;
  height: 0.45rem;
  border-radius: 50%;
}
.content-name
{
  font-size:0.155rem;
}
.content-name>span
{
  padding-left:0.02rem;
  color:orange;
  font-size:0.12rem;
}
.content-intro
{
  width: 100%;
  font-size: 0.115rem;
  color:#ccc;
  height: 0.4rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
/* 精品课程 、推荐课程*/
.pan-good
{
  width: 94%;
  padding:0 3%;
  margin-bottom:0.2rem;
}
.pan-good-title
{
   border-left:3px solid red;
  padding-left:2%;
  font-size:0.16rem;
}
.pan-good-content
{
  width: 100%;
  height: 2rem;
  background-color: #fff;
  margin-top:0.15rem;
  border-radius: 0.1rem;
  display:flex;
  justify-content: center;
}
.pan-good-content-wrapper
{
  width: 94%;
  height: 1.8rem;
  /* background-color: red; */
  margin-top:0.2rem;
}
.pan-good-content-wrapper>div:nth-child(1)
{
  font-size:0.16rem;
  margin-bottom:0.03rem
}
.pan-good-content-wrapper>div:nth-child(2)
{
  font-size:0.13rem;
  color:#414040
}
.pan-good-content-wrapper>div:nth-child(3)
{
  width: 100%;
  height: 0.7rem;
  font-size:0.13rem;
  color:#646363;
  display:flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom:1px solid rgb(233, 230, 230);
}
.pan-good-content-wrapper>div:nth-child(3)>img
{
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 50%;
  margin-right:3%;
}
.pan-good-content-wrapper>div:nth-child(4)
{
  width: 100%;
  height: 0.47rem;
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.wrapper-apply
{
  color:#A1A1A1;
}
.wrapper-offer
{
  color:green;
  font-size:0.165rem;
}
.wrapper-offer-red
{
  color:red;
   font-size:0.165rem; 
}

</style>